import React from 'react';
import './styles/Language.css';
import { toEng, toPl } from '../redux/actions/transAction';
import { connect } from 'react-redux';

const Language = ({ toEng, toPl }) => {
    return (
        <div className='langChoice' >
            <button onClick={toPl}>PL </button>
            <button onClick={toEng}>ENG</button>
        </div>
    );
}

export default connect(null, { toEng, toPl })(Language);