import React, { useState } from 'react';
import axios from 'axios';
import './styles/Contact.css';
import { useFormik } from 'formik';
import Recaptcha from 'react-recaptcha';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';


const validate = values => {
    const errors = {};
    if (!values.firstName) {
        errors.firstName = 'pole wymagane (please fill)';
    } else if (values.firstName.length > 15) {
        errors.firstName = 'za dużo liter (too many letters)';
    }

    if (!values.lastName) {
        errors.lastName = 'pole wymagane (please fill )';
    } else if (values.lastName.length > 20) {
        errors.lastName = 'za dużo liter (too many letters)';
    }

    if (!values.email) {
        errors.email = 'email wymagany (mail required)';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
        errors.email = 'niepoprawny adres email (wrong email address)';
    }
    if (!values.message) {
        errors.message = 'chcesz wysłać bez wiadomości?  (type message)';
    } else if (values.message.length < 10) {
        errors.message = 'napisz coś.. (type something...)';
    }

    return errors;
}

const Contact = ({ translate: { form, firstname, surname, email, mes, submit, human } }) => {
    const formik = useFormik({
        initialValues: {
            firstName: '',
            lastName: '',
            email: '',
            message: ''
        },
        validate,
        onSubmit: values => {
            if (serverState.res) {
                axios({
                    method: "POST",
                    url: process.env.REACT_APP_MY_EMAIL,
                    data: values
                })
                    .then(response => {
                        formik.setSubmitting(false);
                        formik.resetForm();
                        handleServerResponse(true, "Twoja wiadomość została wysłana (Your message was send!)");
                    })
                    .catch(error => {
                        formik.setSubmitting(false);
                        handleServerResponse(false, error.response.data.error);
                    });
            } else { alert(human) }
        },
    });
    // sprawdzanie mounted

    //elemnty formspree
    const [serverState, setServerState] = useState({ ok: '', msg: '', res: '' });

    const handleServerResponse = (ok, msg) => {
        setServerState({ ok, msg });
    };
    //recaptcha
    // const [response, setResponse] = useState(false);

    function callback(value) {
        if (value) {
            setServerState({ res: value })
        };
    }
    return (
        <div className="contactMain">
            <h3>{form}</h3>
            <form onSubmit={formik.handleSubmit}>

                <label htmlFor="firstName">{firstname}</label>
                <input
                    id="firstName"
                    name="firstName"
                    type="text"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.firstName}
                />
                {formik.touched.firstName && formik.errors.firstName ? (
                    <div>{formik.errors.firstName}</div>
                ) : null}


                <label htmlFor="lastName">{surname}</label>
                <input
                    id="lastName"
                    name="lastName"
                    type="text"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.lastName}
                />
                {formik.touched.lastName && formik.errors.lastName ? (
                    <div>{formik.errors.lastName}</div>
                ) : null}


                <label htmlFor="email">{email}</label>
                <input
                    id="email"
                    name="email"
                    type="email"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.email}
                />
                {formik.touched.email && formik.errors.email ? (
                    <div>{formik.errors.email}</div>
                ) : null}

                <label htmlFor="message">{mes}</label>
                <textarea
                    id="message"
                    name="message"
                    type="textarea"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.message}
                />
                {formik.touched.message && formik.errors.message ? (
                    <div>{formik.errors.message}</div>
                ) : null}
                <button type="submit" >{submit}</button>
                {serverState && (
                    <p className={!serverState.ok ? "errorMsg" : ""}>
                        {serverState.msg}
                    </p>
                )}
                <Recaptcha
                    sitekey={process.env.REACT_APP_SITE_KEY}
                    render="automatic"
                    verifyCallback={callback}
                    theme="dark"
                    className="recaptcha-one"
                />
            </form>


        </div>
    );
}
Contact.propTypes = {
    translate: PropTypes.object.isRequired,
}
export default connect(state => ({
    translate: state.translate
})
    , null)(Contact);