import React from 'react';
import './styles/AboutMe.css';
import { fadeInLeftBig, fadeInRightBig } from 'react-animations'
import styled, { keyframes } from 'styled-components';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import damian1 from '../imgs/damian1.png';
import hobby1 from '../imgs/hobby1.jpg';
import hobby2 from '../imgs/hobby2.jpg';
import hobby3 from '../imgs/hobby3.jpg';
import immorise from '../imgs/strona-immorise.jpg';
import takyes from '../imgs/strona-takyes.jpg';
import github from '../imgs/strona-github.jpg';
import dranna from '../imgs/strona-dranna.jpg';
import koparka from '../imgs/strona-koparka.jpg';
import lederg from '../imgs/strona-lederg.jpg';

//dodawanie efektów fade do styled components
const fadeAnimation = keyframes`${fadeInLeftBig}`;
const BouncyDiv = styled.div`
  animation: 2s ${fadeAnimation};
`;
const fadeRight = keyframes`${fadeInRightBig}`;
const BouncyRight = styled.div`
  animation: 4s ${fadeRight};
`;

const AboutMe = ({ translate: { myname, bio, projects, hobbies, guitar, bike, travel } }) => {

    return (
        <>
            <div className="about">
                <BouncyDiv>
                    <div className="mycv">
                        <img src={damian1} alt="moje zdjęcie profilowe" />

                        <p> <span>{myname}</span><br />{bio}</p>
                    </div>
                </BouncyDiv>
                <BouncyRight>
                    <div className="myportfolio">
                        <p>{projects}</p>
                        <ul>
                            <li><a target="_blank" rel="noopener noreferrer" href="https://www.immorise.com"><img src={immorise} alt="zdjęcie strony immorise" /></a></li>
                            <li><a target="_blank" rel="noopener noreferrer" href="https://takyes.edu.pl"><img src={takyes} alt="zdjęcie strony Takyes" /></a></li>
                            <li><a target="_blank" rel="noopener noreferrer" href="https://github.com/haydulski"><img src={github} alt="logo ze strony github" /></a></li>
                            <li><a target="_blank" rel="noopener noreferrer" href="https://dranna.pl"><img src={dranna} alt="logo ze strony dranna.pl" /></a></li>
                            <li><a target="_blank" rel="noopener noreferrer" href="https://lederg.com"><img src={lederg} alt="logo sklepu lederg" /></a></li>
                            <li><a target="_blank" rel="noopener noreferrer" href="https://koparkasuwalki.pl"><img src={koparka} alt="logo strony koparka" /></a></li>
                        </ul>
                    </div>
                </BouncyRight>
                <BouncyDiv>
                    <div className="freetime">
                        <p>{hobbies}</p>

                        <div className="hobbycontainer">

                            <div className="hobby">
                                <img src={hobby2} alt="moje zdjęcie jak gram na gitarze" />
                                <p>{guitar}</p>
                            </div>
                            <div className="hobby">
                                <img src={hobby3} alt="moje zdjęcie na rowerze" />
                                <p>{bike}</p>
                            </div>
                            <div className="hobby">
                                <img src={hobby1} alt="zdjęcie z wycieczki" />
                                <p>{travel}</p>
                            </div>
                        </div>


                    </div>
                </BouncyDiv>
            </div>
        </>
    );
}
AboutMe.propTypes = {
    translate: PropTypes.object.isRequired
}

export default connect(state => {
    return {
        translate: state.translate
    }
}, null)(AboutMe);