import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import './styles/Navigation.css';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

const Navigation = ({ translate: { start, offer, aboutme, contact } }) => {
    const [toggle, setToggle] = useState(false);

    const handleToggle = () => {
        setToggle(!toggle)
    }
    return (
        <div onClick={handleToggle} className={toggle ? "navi active" : "navi"}>
            <p>menu</p>
            <ul>
                <li><NavLink exact to="/">{start}</NavLink></li>
                <li><NavLink exact to="/oferta">{offer}</NavLink></li>
                <li><NavLink exact to="/omnie">{aboutme}</NavLink></li>
                <li><NavLink exact to="/kontakt">{contact}</NavLink></li>
            </ul>
        </div>
    )
}
Navigation.propTypes = {
    translate: PropTypes.object.isRequired
}
export default connect(state => {
    return {
        translate: state.translate,
    }
}, null)(Navigation);