import React, { useState } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import Animation from './components/Animation/Animation';
import Animation2 from './components/Animation2/Animation';
import Navigation from './components/Navigation';
import AboutMe from './components/AboutMe';
import Contact from './components/Contact';
import ContactMobile from './components/ContactMobile';
import Offer from './components/Offer';
import Language from './components/Language';
import { Link } from "react-scroll";
import hammenu from './imgs/hammenu.png';
import LoadingIndicator from './components/LoadingIndicator/LoadingIndicator';

const windowWidth = window.innerWidth;

function App() {
  const [value, changeValue] = useState(1);
  function handleValue() {
    changeValue(value * -1)

  }
  return windowWidth > 860 ?
    (<>
      <React.Suspense fallback={<LoadingIndicator />}>
        <Router>
          <aside><Navigation /></aside>
          <Language />
          <div className="App">
            <Switch>
              <Route exact path="/"><Animation /></Route>
              <Route path="/oferta"><Offer /></Route>
              <Route path="/omnie"><AboutMe /></Route>
              <Route path="/kontakt"><Contact /></Route>
              <Route component={Animation} />
            </Switch>

          </div>
        </Router>
      </React.Suspense>
    </>
    )
    : (
      <div className='containerMobile'>
        {value > 0 ? <button className="navClick" onClick={handleValue}><img src={hammenu} alt="ikona menu" /></button> : null}
        {value < 0 ? <div className="mobileNavigation">
          <button className="navClose" onClick={handleValue}>X</button>
          <ul>
            <li><Link activeClass="active" to="animation" spy={true} smooth={true} duration={1000} onClick={handleValue}>Start</Link></li>
            <li><Link activeClass="active" to="offer" spy={true} smooth={true} duration={1000} onClick={handleValue}>Offer</Link></li>
            <li><Link activeClass="active" to="about" spy={true} smooth={true} duration={1000} onClick={handleValue}>About me</Link></li>
            <li><Link activeClass="active" to="contactMain" spy={true} smooth={true} duration={600} onClick={handleValue} >Contact</Link></li>
          </ul>
        </div> : null}
        <Animation2 />
        <Offer />
        <AboutMe />
        <ContactMobile />
      </div>
    );
}

export default App;
