import React from 'react';
import './styles/Offer.css';
import CountUp from 'react-countup';
import programy from '../imgs/programy.png';
import seologo from '../imgs/seologo.png';
import rwdlogo from '../imgs/rwdlogo.png';
import LoadingIndicator from '../components/LoadingIndicator/LoadingIndicator';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

const Offer = ({ translate: { hello, order, newest, newest1, seo, seo2, rwd, rwd2, price, currency, amount } }) => {
    return !currency ? <LoadingIndicator /> : (
        <div className='offer'>
            <header>{hello}</header>
            <h1>{order}</h1>
            <div className="offermain">
                <div>
                    <h2>{newest}</h2>
                    <img src={programy} alt="zdjęcie języków programowania" />
                    <p>{newest1}</p>

                </div>
                <div>
                    <h2>{seo}</h2>
                    <img src={seologo} alt="logo pozycjonowania strony" />
                    <p>{seo2}</p>

                </div>
                <div>
                    <h2>{rwd}</h2>
                    <img src={rwdlogo} alt="logo responsive web designing" />
                    <p>{rwd2}</p>

                </div>
            </div>
            <div className="promo">
                {price} <span>{<CountUp start={0} end={amount} duration={20} />}</span> {currency}
            </div>
        </div>

    );
}
Offer.propTypes = {
    translate: PropTypes.object.isRequired
}
export default connect(state => {
    return {
        translate: state.translate
    }
}, null)(Offer);