import React, { useState } from 'react';
import axios from 'axios';
import './styles/Contact.css';
import { useFormik } from 'formik';
import humantest from './styles/images/humantest.jpg';

const validate = values => {
    const errors = {};
    if (!values.firstName) {
        errors.firstName = 'please fill';
    } else if (values.firstName.length > 15) {
        errors.firstName = 'too many letters';
    }

    if (!values.lastName) {
        errors.lastName = 'please fill';
    } else if (values.lastName.length > 20) {
        errors.lastName = 'too many letters';
    }

    if (!values.email) {
        errors.email = 'mail required';
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
        errors.email = 'wrong email address';
    }
    if (!values.message) {
        errors.message = 'type message';
    } else if (values.message.length < 10) {
        errors.message = 'type something...';
    }

    return errors;
}

const ContactMobile = () => {

    const formik = useFormik({
        initialValues: {
            firstName: '',
            lastName: '',
            email: '',
            message: ''
        },
        validate,
        onSubmit: values => {
            if (serverState.flag) {
                axios({
                    method: "POST",
                    url: process.env.REACT_APP_MY_EMAIL,
                    data: values
                })
                    .then(response => {
                        formik.setSubmitting(false);
                        formik.resetForm();
                        handleServerResponse(true, "Your message was send!");
                    })
                    .catch(error => {
                        formik.setSubmitting(false);
                        handleServerResponse(false, error.response.data.error);
                    });
            } else { alert('Prove you are human') }
        },
    });
    //elemnty formspree
    const [serverState, setServerState] = useState({ ok: '', msg: '', res: '', flag: false });

    const handleServerResponse = (ok, msg) => {
        setServerState({ ok, msg, flag: false });
    };

    function callback(e) {
        if (e.target.value === process.env.REACT_APP_CAPTCHA) {
            setServerState({ res: e.target.value, flag: true })
        } else {
            setServerState({ res: e.target.value, flag: false })
        }

    }
    return (
        <div className="contactMain">
            <h3>Lets stay in touch</h3>
            <form onSubmit={formik.handleSubmit}>

                <label htmlFor="firstName">Name</label>
                <input
                    id="firstName"
                    name="firstName"
                    type="text"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.firstName}
                />
                {formik.touched.firstName && formik.errors.firstName ? (
                    <div>{formik.errors.firstName}</div>
                ) : null}


                <label htmlFor="lastName">Surname</label>
                <input
                    id="lastName"
                    name="lastName"
                    type="text"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.lastName}
                />
                {formik.touched.lastName && formik.errors.lastName ? (
                    <div>{formik.errors.lastName}</div>
                ) : null}


                <label htmlFor="email">email</label>
                <input
                    id="email"
                    name="email"
                    type="email"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.email}
                />
                {formik.touched.email && formik.errors.email ? (
                    <div>{formik.errors.email}</div>
                ) : null}

                <label htmlFor="message">Message</label>
                <textarea
                    id="message"
                    name="message"
                    type="textarea"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.message}
                />
                {formik.touched.message && formik.errors.message ? (
                    <div>{formik.errors.message}</div>
                ) : null}
                {!serverState.flag &&
                    <>
                        <label htmlFor="humantest"> Type this text to prove that you are human:</label>
                        <img className='captchatest' src={humantest} alt="tekst do przepisania" />
                        <input
                            type='text' id='humantest' name='humantest' value={serverState.res} onChange={e => callback(e)} />
                    </>}
                <button type="submit" >Submit</button>
                {serverState && (
                    <p className={!serverState.ok ? "errorMsg" : ""}>
                        {serverState.msg}
                    </p>
                )}

            </form>


        </div>
    );
}

export default ContactMobile;